import Vue from 'vue'
import App from './App'
import router from './router/router.js'
import Vuex from 'vuex'
Vue.use(Vuex)
// mui
import mui from './assets/mui/js/mui.js'
Vue.prototype.mui = mui;

import './assets/mui/css/mui.min.css'

import './assets/mui/css/icons-extra.css'

