import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(MintUI)
// 
Vue.config.productionTip = false

// 
import VuePreview from 'vue2-preview'
Vue.use(VuePreview)
// 
import {
  formatDate
} from './assets/comment/data.js'

/* eslint-disable no-new */
Vue.filter('formatDate', function (data) {
  var date = new Date(data);
  return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
})

// ,car

var Star_car = JSON.parse(localStorage.getItem('car') || '[]');
// vm.$store.commit('getAll_Price',goodinfo); // 
// console.log(this) 

var store = new Vuex.Store({
  state: {
    car: Star_car, //  { id : id , count : , price :  , selected : false  }
    total: 0,
    map : {},     // id  
    map1 : {},    // id  
    tot_price : 0,
    tot_item : 0,
  },
  mutations: {
    // 
    addToCar: function (state, goods) {
      var flag = false;
      state.car.some(item => {
        if (item.id === goods.id) {
          item.count += parseInt(goods.count);
          flag = true
          return true;
        }
      })

      if (!flag) {
        state.car.push(goods);
      }
      localStorage.setItem('car', JSON.stringify(state.car)); // car
    },
    // 
    update(state,option){        //muiBox
        var sum = 0;
        state.car.forEach(item => {
            if(item.id == option.id){
              item.count = option.count;
            }
            sum +=item.count; 
        })
        state.total = sum;
        localStorage.setItem('car', JSON.stringify(state.car));
    },
    createMap(state){
        //    id   
        //                         id    
        state.car.forEach(ele => {
            state.map[ele.id] = ele.count;
            state.map1[ele.id] = ele.maxn;
            // console.log('Map1=='+state.map1[ele.id])
        })
    },
    deletelist(state,id){    //  localStorage
        var newcar = [];
        var sum = 0;
        state.car.forEach(el => {
            if(el.id === id){

            }else{
              newcar.push(el);
              sum+=el.count;
            }
        })
        state.total = sum;
        state.car = newcar;  
        localStorage.setItem('car', JSON.stringify(state.car)); // car
    },
    
  },

  getters: {
    Total: function (state) {
      var sum = 0;
      state.car.forEach(item => {
        sum += parseInt(item.count);
      })
      setTimeout(() => {
        state.total = sum;
      }, 1000);
      return state.total;
    },
    getMap : function(state){
      return state.map;
    },
    // 
    getAll_Price(state){
      var account = {
        tmp : 0,
        tmp_All : 0,
      }
      state.car.forEach(el => {
            if(el.selected){
                account.tmp_All += parseInt(el.price) * parseInt(el.count);
                account.tmp+=el.count;
            }
      });
      localStorage.setItem('car', JSON.stringify(state.car)); // car
      return account;
    }
  }
});
var vm = new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>',
  filters: {
    formatDate(time) {
      var date = new Date(time);
      return formatDate(date, 'yyyy-MM-dd');
    }
  },
  store,
  
  methods: {
    // 
    
  },
})



