<template>
    <div class="GoodsDesc">
        <h3>{{newlist[0].title}}</h3>
        <hr>
        <div class="container_descc">
            <p class="tell">
                ,, ,,
                ,
            </p>
            <pre>
                ,
                6.1
                LCDIPS
                1792x828
                3110mAh
                Liquid 
                1400:1  ()
                
                 (P3)
                
                625  ()
                
                
                Qi 
                18W ) 
                4G:TD-LTETD-LTE FDD-LTE 
                TD-LTE FDD-LTE
            </pre>
            <img :src="newlist[0].img_url" alt="">
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            id : this.$route.params.id,
            falist : this.$route.params.falist,
            newlist : [] || falist[0],
        }
    },
    created(){
        this.getGoodsDesc();
    },
    methods : {
        getGoodsDesc(){
            // id
            for(var el in this.falist){
                if(this.falist[el].id === this.id){
                    this.newlist = [],
                    this.newlist.push(this.falist[el]);
                }
            }
        }
    },
}
</script>


<style scoped>
.GoodsDesc h3{
    font-size: 15px;
    text-align: center;
    color: rgb(84, 209, 209);
    margin: 15px 0;
}    
.container_descc{
    padding: 0px 10px;
}
.tell{
    color: red;

}
pre{
    font-size: 12px;
}
.container_descc img{
    width: 100%;
}
</style>


