<template>
    <div class="cmt-container">
        
        <h3></h3>
        <hr>
        <textarea placeholder="(120)" maxlength="120" v-model="msg">

        </textarea>
        <mt-button type='primary' size='large' @click="postComments"></mt-button>
        <div class="cmt-list">
            <div class="cmt-item" v-for=" (item ,i) in comments" :key="item.id">
                <div class="cmt-title">
                    {{i+1}}&nbsp;&nbsp;:{{item.user_name}}&nbsp;&nbsp;:{{item.add_time | formatDate}}
                </div>
                <div class="cmt-body">
                    {{item.content}}
                </div>
            </div>
            
        </div>
        <mt-button type='danger' size='large' plain @click='showMore'></mt-button>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';

export default {
    data() {
        return {
            pageindex : 1,
            comments : [],
            sum : 10, // 
            msg : "",   //
            flag : false,
        }
    },
    props : ['id'],
    created(){
        this.getComments();
    },
    methods : {

        getComments(){
            // this.$http.get('api/getcomments/'+"this.id"+"?pageindex"+this.pageindex).then( result => {
            //     if(result.body.status ===0){
            //         this.comments = this.comments.concat(result.body.message);
            //     }else{
            //         Toast("");
            //     }
            // })
            // 
            this.comments = this.comments.concat([
                { id :1 + this.sum, user_name : "", add_time : '2019-11-05 17:12:44' , content :'' },
                { id :2 + this.sum, user_name : "", add_time : '2019-11-05 17:11:44' , content :'' },
                { id :3 + this.sum, user_name : "", add_time : '2019-11-22 17:12:44' , content :'' },
                { id :4 + this.sum, user_name : "", add_time : '2019-11-11 17:12:44' , content :'' },
                { id :5 + this.sum, user_name : "", add_time : '2019-11-15 17:12:44' , content :'' },
                { id :6 + this.sum, user_name : "", add_time : '2019-12-05 17:12:44' , content :'' },
                { id :7 + this.sum, user_name : "", add_time : '2019-12-05 16:12:44' , content :'' },
                { id :8 + this.sum, user_name : "daydaylee", add_time : '2019-09-05 17:12:44' , content :'' },
                { id :9 + this.sum, user_name : "Tony", add_time : '2019-12-05 15:13:44' , content :'' },
                { id :10 + this.sum, user_name : "BeauTiy", add_time : '2019-12-05 17:13:54' , content :'' },
            ]);
            
        },
        showMore(){
            this.pageindex++;
            this.sum+=10;
            this.getComments();
        },
        postComments(){
            this.flag = true;
            // 
            if(this.msg.trim().length ===0 &&this.flag){
                return Toast(",");
            }
            // 
            // 
            //  URL
            //   {content : this.msg}
            //   
            // this.$http.post('api/postcomment/:'+this.$route.params.id,{
            // },{ "content " : this.msg.trim() },{'emulateJSON': true,})
            // .then( result =>{
            //     if(result.body.status ===0 ){
            //         //  
            //         var cmt = { user_name : '', add_time : Date.now(), content : this.msg.trim()};
            //         this.comments.unshift(cmt);
            //         this.msg = '';
            //     }
            // });
            var cmt = { user_name : '', add_time : Date.now() , content : this.msg.trim()};
            this.comments.unshift(cmt);
            this.msg = '';
        }
    },

}
</script>

<style scoped >
.cmt-container{
    
}
.cmt-container h3{
    font-size: 18px;
}
.cmt-container textarea{
    font-size: 14px; 
    height: 85px;
    margin: 0;
    border-radius: 5px;
}
.cmt-list{
    
    font-size: 12px;
    margin: 5px 0;
}
.cmt-title{
    line-height: 35px;
    background: #ccc;
    border-radius: 5px;
}
.cmt-body{
    margin: 5px 0px;
    padding-left: 10px;
    border-radius: 5px;
}
/* .mint-toast-text{
    color: red!important;
} */
</style>
<style >
.mint-toast-text{
    color: red!important;
    border-radius: 5px;
}
</style>


